:root {
  /* Size */
  --height-header: 12vh;
  --height-footer: 15vh;
  --max-width: 1300px;
  --padding-x: 2rem;
  --size-title-h1: 2.7rem;
  --size-title-h2: 1.8rem;
  --size-menu: 1rem;
  --size-paragraph: 1rem;
  --line-height: 1.75;
  --opacity: 0.8;

  /* Typography */
  --font-menu: "Space Mono", monospace;
  --font-paragraph: "Space Mono", monospace;
  --font-title: "Rubik", sans-serif;

  /* Colors */
  --white: #ffffff;
  --black: #222222;
  --blue: #253cd9;
  --pink: #e71a88;
  --gray: #313131;
  --html: #f4470b;
  --css: #0068ba;
  --js: #e9d44d;
  --bootstrap: #754fad;
  --sass: #ce6b9e;
  --react: #00d1f7;
  --instagram: #f71e9e;
  --twitter: #1d9bf0;
  --linkedin: #016fab;
  --github: #000000;
  --codepen: #222222;
}
.paragraph {
  font-family: "Space Mono", monospace;
  font-family: var(--font-paragraph);
  font-size: 1rem;
  font-size: var(--size-paragraph);
  line-height: 1.75;
  line-height: var(--line-height);
  color: #313131;
  color: var(--gray);
  opacity: 0.8;
  opacity: var(--opacity);
  margin: 0 0 1rem 0;
}

