  justify-content: flex-end;
  align-items: flex-start;
  flex-direction: column;
  border-radius: inherit;
  position: absolute;
  top: 0;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.5s, visibility 0.5s;
